import React from 'react';
import s from './Dialogs.module.css';
import DialogItem from './DialogItem/DialogItem';
import DialogsMessage from './Message/DialogsMessage';
import { reduxForm, Field } from 'redux-form';
import { required, maxLengthCreator } from '../../until/validation/validation';
import { Textarea } from '../common/FormsControls/FormsControls';





const Dialogs = (props) => {

  let state = props.dialogsPage;
 
  let dialogsElements = state.dialogs.map(d => <DialogItem name={d.name} key = {d.id} id={d.id} />);
  let messagesElemants = state.messages.map(m => <DialogsMessage message={m.message} key = {m.id} />);



  let AddNewMessage = (value) => {
    props.addMessage(value.newMessage)
  }


  return (
    <div className={s.dialogs}>
      <div className={s.dialogsItems}>
        {dialogsElements}
      </div>
      <div className={s.messages}>
        <div>{messagesElemants}</div>
         <AddMessageReduxForm onSubmit={AddNewMessage}/>
      </div>
    </div>

  )

}

const maxLength100 = maxLengthCreator(100);

const AddMessageForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <div><Field placeholder="Your Text" name="newMessage" component={Textarea} validate={[required, maxLength100]} /></div>
      <div><button>Send</button></div>
    </form>
  )
}

const AddMessageReduxForm = reduxForm({ form: 'messages' })(AddMessageForm)


export default Dialogs;